    padding-left: 15px;
    width: 100%;
    height: 35px;
    color: #fddb89;
    font-size: 17px;
    line-height: 35px;
    background: #2f435a;
    border: none;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
    border-radius: 50px;
    outline: 0;
  }

  #searchBarList::placeholder {
    color: rgba(255, 231, 190, 0.79);
  }

  .material-symbols-rounded {
    font-size: 26px;
    color: #fdce6d;
    position: absolute;
    right: 0;
  }

  #tableClient {
    display: flex;
    position: relative;
    justify-content: center;
    width: 90%;
    margin-top: 15px;
    padding: 5px;
    border-collapse: collapse;
    height: 435px;
    background: rgba(47, 67, 90, 0.05);
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
  }

  .thead {
    padding-left: 10px;
    position: absolute;
    width: 98%;
    border-collapse: collapse;
  }

  .thead-tr {
    display: flex;
    gap: 5px;
    border-collapse: collapse;
  }

  .th {
    align-self: flex-start;
    min-width: 100px;
    max-width: 100px;
    color: #2f435a;
    text-align: start;
    font-size: 16px;
    border-collapse: collapse;
  }

  .th-servicos {
    display: flex;
    width: 100px;
  }

  .th-nome-cliente {
    display: none;
  }

  .th-nome-pet {
    display: none;
  }

  .th-telefone-cliente {
    display: none;
  }

  .tbody {
    display: flex;
    flex-direction: column;
    width: 99%;
    height: 400px;
    padding: 5px;
    margin-top: 21px;
    border: 2px solid #2f435a;
    filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
    border-radius: 10px;
    overflow-y: auto;
    overflow-x: auto;
    border-collapse: collapse;
  }

  .tbody>tr {
    display: flex;
    flex-direction: column;
    padding: 5px;
    height: 180px;
    width: 100%;
    gap: 0;

    border-bottom: 1px solid #ced5dd;
  }

  .tbody>tr:hover {
    border: none;
    background-color: transparent;
    font-size: 16px;
  }


  .td {
    min-width: min-content;
    max-width: max-content;
    display: flex;
    width: 90%;
    padding: 0;
    margin: 0;
    height: 25px;
  }

  .td-nome {
    width: 80%;
    height: 25px;
    padding: 0;
    align-self: flex-start;
  }

  .td-lista {
    width: 80%;
    height: 25px;
    padding: 0;
    align-self: flex-start;
  }

  .td-nome-pet {
    height: 25px;
    padding: 0;
    align-self: flex-start;
  }

  .td-celular {
    height: 25px;
    padding: 0;
    align-self: flex-start;
  }

  /*menu-lateral*/
  #slide {
    display: none;
    flex-direction: column;
    gap: 20px;
    position: fixed;
    align-items: flex-start;
    top: 0;
    left: 0;
    padding: 40px 20px;
    background-color: #fdce6d;
    height: 100%;
    width: 200px;
    z-index: 1000;
    filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
    border-radius: 0px 10px 10px 0px;
  }

  #slide>li {
    display: flex;
    list-style-type: none;
    align-items: center;
    margin-bottom: 20px;
  }

  #slide>li:hover>.drop-down-agenda {
    display: block;
    position: absolute;
    left: 170px;
    top: 95px;
    width: 210px;
    background-color: rgb(255, 231, 190);
    padding: 7px;
    border-radius: 8px;
    z-index: 100;
    filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.1));
  }

  #slide>li:hover>.drop-down-cadastro {
    display: block;
    position: absolute;
    left: 170px;
    top: 135px;
    width: 210px;
    background-color: rgb(255, 231, 190);
    padding: 7px;
    border-radius: 8px;
    z-index: 90;
    filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.1));
  }

  #slide>li>a {
    display: flex;
    align-items: center;
    line-height: 18px;
    color: #2f435a;
    text-decoration: none;
    font-size: 16px;
    margin-left: 10px;
  }

  .menu-container {
    display: flex;
  }

  .um {
    margin-left: 55px;
  }

  .dois {
    margin-left: 30px;
  }

  .drop-down-agenda {
    display: none;
    position: absolute;
    left: 210px;
    top: 135px;
    width: 210px;
    background-color: rgb(255, 231, 190);
    padding: 7px;
    border-radius: 8px;
    z-index: 100;
    filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.1));
  }

  .drop-down-agenda>ul {
    list-style-type: none;
  }

  .drop-down-agenda>ul>li {
    margin-bottom: 8px;
  }

  .drop-down-agenda>ul>li>a {
    color: #2f435a;
    font-size: 18px;
    text-decoration: none;
  }

  .drop-down-agenda>ul>li>a:hover {
    filter: drop-shadow(1px 5px 10px rgba(0, 0, 0, 0.5));
  }

  .drop-down-cadastro {
    display: none;
    position: absolute;
    left: 210px;
    top: 135px;
    width: 210px;
    background-color: rgb(255, 231, 190);
    padding: 7px;
    border-radius: 8px;
    filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.1));
  }

  .drop-down-cadastro>ul {
    list-style-type: none;
  }

  .drop-down-cadastro>ul>li {
    margin-bottom: 8px;
  }

  .drop-down-cadastro>ul>li>a {
    color: #2f435a;
    font-size: 18px;
    text-decoration: none;
  }

  .drop-down-cadastro>ul>li>a:hover {
    filter: drop-shadow(1px 5px 10px rgba(0, 0, 0, 0.5));
  }

  .fa-home,
  .fa-power-off {
    color: #2f435a;
    font-size: 23px;
  }

  .fa-headset {
    font-size: 22px;
    color: #2f435a;
  }

  .fa-calendar {
    font-size: 22px;
    padding-left: 3px;
    color: #2f435a;
  }

  .fa-user-group {
    font-size: 21px;
    color: #2f435a;
  }

  .fa-file-lines {
    font-size: 24px;
    padding-left: 3px;
    color: #2f435a;
  }

  /*menu-lateral*/

